/**
* @author shelly
* @description 授权弹窗界面
* @date 2017-05-12
**/
import React from 'react';
import { message, Button, Alert, Modal } from 'antd';

import './accredit_popup.css'; 

class Accredit extends React.Component {
  constructor(props){
    super(props);
    this.state={username:'',password:'',visible:false};
  };

// 点击确定按钮
  handleOk=()=>{ 
    if(this.state.username==''|| this.state.password==''){
      this.setState({visible:true})
    }else{
      this.setState({visible:false})
    }
  }

// 点击数字键盘
 handleClick=(e)=> {
    let value=e.target.innerHTML;
    if(this.state.position=='username'){
         let inputVal=this.state.username.concat(value);
         this.setState({username:inputVal});
    }else{
         let inputVal=this.state.password.concat(value);
         this.setState({password:inputVal});
    }
  }

// 点击退格
  handleBack=(e)=>{
    if(this.state.position=='username'){
      let inputVal=this.state.username;
      inputVal=inputVal.substring(0,inputVal.length-1);
      this.setState({username:inputVal})
    }else{
      let inputVal=this.state.password;
      inputVal=inputVal.substring(0,inputVal.length-1);
      this.setState({password:inputVal})
    }
  }

  // 点击清空
  handleClear=(e)=>{
    if(this.state.position=='username'){
      let inputVal='';
      this.setState({username:inputVal})
    }else if(this.state.position=='password'){
      let inputVal='';
      this.setState({password:inputVal})
    }
  }

  //点击关闭的按钮
  handleCancel=(e)=>{
    if(this.props.onCancel){
				this.props.onCancel();
		}
  }

  render() {
    message.config({
      top: 100,
      duration: 200,
    });
   

    let  alertBlock;
    if(this.state.visible){
        alertBlock = <Alert message={'你没有【'+this.props.title+'】权限，请联系有权人授权！'} type="warning" showIcon className="warning"/>
    }
    let Login;
    
      Login=<div id="login">
              {alertBlock}
              <input type="text" value={this.state.username} placeholder="账号" id="account-num" onChange={(e)=>{
                  this.setState({username:e.target.value});
              }} onFocus={()=>{
                this.setState({visible:false})
                this.setState({position:"username"})
              }}/>
              <input type="password" value={this.state.password} placeholder="密码" id="password" onChange={(e)=>{
                  this.setState({password:e.target.value});
              }} onFocus={()=>{
                this.setState({visible:false})
                this.setState({position:"password"})
              }}/>
            </div>;
    

 
    return (
      <div id="accredit">
        <Modal id="accredit-modal" title="授权" 
            visible={true} 
            maskClosable={false} 
            okText='确定' cancelText='放弃'
            width={510}
            height={546} 
            footer={null}
            wrapClassName="accredit-popup-modal"
            onCancel={this.handleCancel}
           
        >
          {Login}
          <div id="num-key">
            <ul>
              <li className="number" onClick={this.handleClick}>1</li>
              <li className="number" onClick={this.handleClick}>2</li>
              <li className="number" onClick={this.handleClick}>3</li>
              <li className="back iconfont icon-order_btn_back" onClick={this.handleBack}></li>
              <li className="number" onClick={this.handleClick}>4</li>
              <li className="number" onClick={this.handleClick}>5</li>
              <li className="number" onClick={this.handleClick}>6</li>
              <li className="clear-all" onClick={this.handleClear}>清空</li>
              <li className="number" onClick={this.handleClick}>7</li>
              <li className="number" onClick={this.handleClick}>8</li>
              <li className="number" onClick={this.handleClick}>9</li>
              <li className="cancle" onClick={()=>{
                   if(this.props.onCancel){
                        this.props.onCancel();
                      }
              }}>取消</li>
              <li className="zero" onClick={this.handleClick}>0</li>
              <li className="use-card">刷卡</li>
              <li className="confirm" onClick={this.handleOk}>确定</li>
            </ul>
          </div>
        </Modal>
      </div>
    )
  }
}

export default Accredit;


