import React from 'react';

import './beizhu.css';
class BeiZhu extends React.Component {

  //  remarkValue 备注内容
  state = {memo: this.props.memo || ''}

  componentWillReceiveProps(props) {
    if(this.props !== props) this.setState({memo: props.memo});
  }

  render() {

    return (
        <div className="remarks">
          <input type="text" className="remark-input" value={this.state.memo} onChange={(e)=>{
              this.props.onEntry && this.props.onEntry(e.target.value);
          }}/>
          <i className="iconfont icon-shanchu1" onClick={()=>{
              this.props.onEntry && this.props.onEntry('');
          }}></i>
        </div>
    );
  }
}



export default BeiZhu;



