import React from 'react';
import { inject, observer } from 'mobx-react';
import { Modal, Alert, Tabs, Rate } from 'antd';
import classnames from 'classnames';

import CommonKeyboard from '../common-keyboard';
import MyScroll from 'components/my-scrollbar';
import VerticalTabs from 'components/vertical-tabs';
import BeiZhu from '../beizhu';
import ChangeNumber from 'components/change-number';
import { getJSON } from 'common/utils';

import './not_order_popup.css';

const TabPane = Tabs.TabPane;

//未下单弹窗
@inject('dishesStore') @observer
class NotOrderPopup extends React.Component {

    constructor(props, context) {
        super(props, context);

        const {dishesStore, dishes} = props;

        let activeKey = (!dishes.comboList && !dishes.variantID) ? 'quantity' : 'tag';

        const productMessage = dishesStore.productMessageMap[dishes.cartRecordID];
        if(productMessage) {
            const { 
                specificationList, //规格可选列表
                attributeMap, //口味做法可选列表
                designers //厨师可选列表
            } = productMessage;
            if(specificationList && !!specificationList.length) {
                activeKey = 'specification';
            }else if(attributeMap && Object.keys(attributeMap).length) {
                activeKey = 'attribute';
            }else if(designers && !! designers.length) {
                activeKey = 'designer';
            }
        }

        this.state = {
            type: activeKey,
            ...productMessage,
            handselQuantity: 1,
            quantity: dishes.quantity,
            inputType: dishes.needWeigh ? 'weight' : 'quantity'
        }
    }

    //确定
    handleOk=()=>{
        let {dishesStore, dishes, closeHandle} = this.props;
        let cartRecordID = dishes.cartRecordID;
        let {
            type,
            attributeMap,
            selectedAttributeList,
            optionID,
            optionName,
            optionPrice,
            quantity,
            weight,
            designerID,
            tagIDs,
            produceStatus,
            handselReason,
            handselMemo,
            handselQuantity,
            tableID,
            tableName,
            price
        } = this.state;

        switch(type) {
            case 'specification':
                //修改规格
                dishesStore.updateProductToCart({
                    cartRecordID,
                    optionID, 
                    optionName, 
                    optionPrice
                });
                break;
            case 'quantity':
                console.log('quantity: ',quantity)
                //修改数量
                if(!Number(quantity)) {
                    dishesStore.showFeedback({
                        status: 'validate', 
                        msg: '请输入数量！'
                    });
                    return;
                }
                if(dishes.needWeigh&&!Number(weight)) {
                    dishesStore.showFeedback({
                        status: 'validate', 
                        msg: '请输入重量！'
                    });
                    return;
                }

                dishesStore.updateProductToCart({
                    cartRecordID, 
                    quantity,
                    weight
                });
                
                break;
            case 'attribute':
                //修改口味做法
                dishesStore.updateProductToCart({
                    cartRecordID, 
                    attributeMap, 
                    selectedAttributeList
                });
                break;
             case 'designer':
                //修改厨师
                if(designerID) {
                    dishesStore.updateProductToCart({
                        cartRecordID, 
                        designerID
                    });
                }else {
                    dishesStore.showFeedback({
                        status: 'validate', 
                        msg: '请选择厨师！'
                    });
                    return;
                }
                break;
            case 'tag':
                //厨房状态
                if(tagIDs || produceStatus === 694) {
                    dishesStore.updateProductToCart({
                        cartRecordID, 
                        tagIDs, 
                        produceStatus
                    });
                }else {
                    dishesStore.showFeedback({
                        status: 'validate', 
                        msg: '至少选择一项状态！'
                    });
                    return;
                } 
                break;
            case 'handsel':
                //赠菜
                if(handselReason|| handselMemo){
                    if(dishes.quantity === handselQuantity) {
                        //全部赠菜
                        let memo = handselReason || handselMemo;
                        dishesStore.updateProductToCart({
                            cartRecordID, 
                            aLaCarteMethod: 686,
                            memo
                        });
                    }else {
                        //部分赠菜
                        dishesStore.updateProductToCart({
                            cartRecordID, 
                            quantity: dishes.quantity-handselQuantity
                        });
                        
                        let newDishes = Object.assign({}, dishes);
                        newDishes.cartRecordID = Math.floor(Math.random()*10000000000); //删除cartRecordID，以便重新分配
                        newDishes.memo = newDishes.memo ? newDishes.memo+handselReason : handselReason;
                        newDishes.quantity = handselQuantity;
                        newDishes.aLaCarteMethod = 686; //赠菜
                        newDishes.originRecordID = dishes.cartRecordID; //原菜品ID

                        //复制菜品配置信息并添加到购物车
                        dishesStore.cloneToCart({
                            newProduct: newDishes, 
                            originProduct: dishes
                        });
                    }
                }else {
                    dishesStore.showFeedback({
                        status: 'validate', 
                        msg: '请选择赠菜原因！'
                    });
                    return;
                }
                break;
            case 'cancelHandsel':
                //取消赠菜
                if(handselQuantity>dishes.quantity) {
                    dishesStore.showFeedback({
                        status: 'validate', 
                        msg: '取消数量大于可取消数量！'
                    });
                    return;
                }

                let originProduct;
                dishesStore.shoppingCart.forEach((cartRecord, index) => {
                    if(cartRecord.cartRecordID === dishes.originRecordID) {
                        originProduct = cartRecord;
                    }
                    if(cartRecord.comboList) {
                        cartRecord.comboList.forEach((combo, index) => {
                            if(combo.cartRecordID === dishes.originRecordID) {
                                originProduct = combo;
                            }
                        });
                    }
                });

                let originQuantity = originProduct.quantity;
                if(originProduct.aLaCarteMethod!==686) {
                    //有未赠送的原菜品
                    dishesStore.updateProductToCart({
                        cartRecordID: originProduct.cartRecordID,
                        quantity: originQuantity+handselQuantity*1,
                        memo: ''
                    });
                    if(dishes.quantity === handselQuantity) {
                        //全部取消赠菜
                        dishesStore.deleteFromCart(dishes.cartRecordID);
                    }else {
                        //部分取消赠菜
                        dishesStore.updateProductToCart({
                            cartRecordID: dishes.cartRecordID,
                            quantity: dishes.quantity-handselQuantity
                        }); 
                    }
                }else {
                    //没有未赠送的原菜品
                    if(dishes.quantity === handselQuantity) {
                        //全部取消赠菜
                        dishesStore.deleteFromCart(dishes.cartRecordID);
                    }else {
                        //部分取消赠菜
                        dishesStore.updateProductToCart({
                            cartRecordID: dishes.cartRecordID,
                            quantity: dishes.quantity-handselQuantity
                        }); 
                    }

                    let newDishes = Object.assign({}, dishes);
                    newDishes.cartRecordID = Math.floor(Math.random()*10000000000);
                    newDishes.quantity = handselQuantity;
                    newDishes.aLaCarteMethod = 684; //正常点菜
                    newDishes.memo = '';
                    
                    //复制菜品配置信息并添加到购物车
                    dishesStore.cloneToCart({
                        newProduct: newDishes, 
                        originProduct: dishes
                    });
                    
                    //修改菜品originRecordID
                    dishesStore.changeOriginRecordID({
                        oldOriginRecordID: dishes.originRecordID, 
                        newOriginRecordID: newDishes.cartRecordID
                    });
                }
                break;
            case 'donated':
                //转赠
                if(tableID) {
                    dishesStore.updateProductToCart({
                        cartRecordID, 
                        tableID,
                        tableName,
                        aLaCarteMethod: 998 //转赠
                    }); 
                }else {
                    dishesStore.showFeedback({status: 'validate', msg: '请选择要转的桌台！'});
                    return;
                }
                break;
            case 'price':
                //临时改价
                if(price) {
                    dishesStore.updateProductToCart({
                        cartRecordID, 
                        price
                    }); 
                }else {
                    dishesStore.showFeedback({
                        status: 'validate', 
                        msg: '请输入修改价格！'
                    });
                    return;
                }
                break;
            default:
        }
        
        //关闭未下单修改弹窗
        closeHandle && closeHandle();
    }

    //放弃
    handleCancel=()=>{
      let { closeHandle } = this.props;
      //关闭未下单修改弹窗
      closeHandle && closeHandle();
    }

    //获取已下单或者正在使用的桌台列表 
    getUsingTableList(tableName) {
        let { dishesStore } = this.props;
        let _this = this;
        getJSON({
            url: '/reception/product/searchTables',
            data: {tableName},
            success: function(json){
                if(json.code === 0) {
                    _this.setState({usingTableList: json.data});
                }else {
                    dishesStore.showFeedback({
                        status: 'warn', 
                        msg: json.message
                    });
                }
            }
        });
    }

    //获取赠菜原因列表
    getHandselReasonList() {
        let { dishesStore } = this.props;
        let _this=this;
        getJSON({
            url: '/reception/product/getPresentFood',
            success: function(json){
                if(json.code === 0) {
                    _this.setState({
                        handselReasonList: json.data
                    });
                }else {
                    //获取失败反馈
                    dishesStore.showFeedback({status: 'warn', msg: json.message});
                }
            }
        });
    }

    componentWillUnmount() {
        this.props.dishesStore.closeFeedback();
    }

    render() {
        const {dishesStore, dishes} = this.props;
        const { 
            weight,
            specificationList, //规格可选列表
            attributeMap, //口味做法可选列表
            designers, //厨师可选列表
            optionID, //已选规格
            selectedAttributeList, //已选口味做法
            handselReasonList,
            handselReason,
            handselMemo,
            memo, //备注
            tagIDs, //当前标签ID
            produceStatus, //当前状态
            designerID, //已选厨师ID
            usingTableList, //使用中的桌台列表
            tableID,
            tableName
        } = this.state;

        let dishesName = dishes.variantName ? dishes.variantName : dishes.productName;

        let activeKey;
        if(specificationList && !!specificationList.length) {
           activeKey = 'specification';
        }else if(!dishes.comboList && !dishes.variantID) {
            activeKey = 'quantity';
        }else if(attributeMap && Object.keys(attributeMap).length) {
            activeKey = 'attribute';
        }else if(designers && !! designers.length) {
            activeKey = 'designer';
        }else {
            activeKey = (!dishes.comboList && !dishes.variantID) ? 'quantity' : 'tag';
        }

        //选厨师
        return <Modal 
            title={dishesName}
            visible={true}
            maskClosable={false}
            onOk={this.handleOk}
            onCancel={this.handleCancel}
            okText='确定' 
            cancelText='取消'
            width={840} 
            wrapClassName="not-order-popup-modal"
        >

            {dishesStore.feedback && dishesStore.feedback.status === 'validate' &&
            <Alert 
                message={dishesStore.feedback.msg} 
                banner 
                closable 
                onClose={() => {
                    //关闭警告信息
                    dishesStore.closeFeedback();
            }} />}
            <Tabs defaultActiveKey={activeKey} onChange={(key) => {
                dishesStore.closeFeedback();
                this.setState({type: key});

                if(key === 'donated') {
                    this.getUsingTableList();
                }
                if(key === 'handsel') {
                    this.getHandselReasonList();
                }
            }}>

                {specificationList && !!specificationList.length &&
                <TabPane tab="规格" key="specification">
                    <div className="guige">
                    {specificationList.map(specification => {
                        return <div
                        key={specification.optionID}
                        className={classnames({
                            'myBtn-365-38': true,
                            'btn-active': specification.optionID === optionID
                        })} onClick={() => {
                            //选择规格
                            this.setState({
                                optionID: specification.optionID,
                                optionName: specification.optionName,
                                optionPrice: specification.price
                            });
                        }}>
                        {specification.price}元／{specification.optionName}
                        </div>
                    })}
                    </div>
                </TabPane>}

                {(!dishes.comboList && !dishes.variantID) &&
                <TabPane tab="数量" key="quantity">
                    <div className="cheng-zhong-block">
                    {!dishes.needWeigh && <div className="shuliang-title">
                        <input type='text' className='result' readOnly value={this.state.quantity} /> 
                        <p className="danwei">单位：{dishes.unit}</p>
                    </div>}
                    {dishes.needWeigh && <div className="chengzhong-title">
                        <input type='text' className='result quantityInput' readOnly value={this.state.quantity} /> <span className="unit">份</span>
                        <span className="multiplication">X</span>
                        <input type='text' className='result weightInput' readOnly value={weight}/> <span className="unit">{dishes.unit}</span>
                    </div>}
                    <CommonKeyboard getValue={(value)=>{
                        let { inputType, quantity, weight } = this.state;
                        
                        if(inputType === 'quantity') {
                            if(typeof value === 'undefined') {
                                this.setState({quantity: quantity.substr(0,quantity.length-1)});
                            }else {
                                this.setState({quantity: quantity+value});
                            }
                        }else {
                            if(typeof value === 'undefined') {
                                this.setState({weight: weight.substr(0,weight.length-1)});
                            }else {
                                this.setState({weight: weight+value});
                            }
                        }
                    }} />
                    </div>
                </TabPane>}

                {attributeMap && Object.keys(attributeMap).length &&
                <TabPane tab="口味做法" key="attribute">
                    <div className="kouwei">
                    <VerticalTabs>
                    {Object.keys(attributeMap).map((key, index)=>{
                        return <TabPane tab={key} key={index}>
                        <MyScroll width={640} height={200}>
                        {attributeMap[key].map((attribute, index)=>{
                            return <li
                            key={index}
                            className={classnames({
                                "myBtn-120-38": true,
                                "myBtn-120-38-selected": attribute.selected
                            })} onClick={() => {
                                //选择口味做法
                                let valueID = attribute.valueID;
                                let _attributeMap = {};
                                let _selectedAttributeList = [];
                                Object.keys(attributeMap).forEach(key => {
                                    _attributeMap[key]=attributeMap[key].map(attribute => {
                                        if(attribute.groupID === 822) {
                                            //口味支持多选
                                            if(attribute.valueID === valueID) attribute.selected = !attribute.selected;
                                        }else {
                                            //其他做法只能单选
                                            attribute.selected = attribute.valueID === valueID;
                                        }
                                        if(attribute.selected) {
                                            _selectedAttributeList.push(attribute);
                                        }
                                        return attribute;
                                    });
                                });
                                this.setState({
                                    attributeMap: _attributeMap,
                                    selectedAttributeList: _selectedAttributeList
                                });
                            }}>
                            {attribute.attributeName}
                            </li>
                        })}
                        </MyScroll>
                        </TabPane>
                    })}
                    </VerticalTabs>
                    <div className="select-items">
                        <div className="select-header">已选口味做法</div>
                        <MyScroll width={800} height={106}>
                        {selectedAttributeList && selectedAttributeList.length ? 
                        selectedAttributeList.map((attribute, index)=>{
                        return  <li 
                            key={index} 
                            className="myBtn-120-38-selected"
                        >
                            {attribute.attributeName}
                            <i className="iconfont icon-shanchu1" onClick={() => {
                                let attributeID = attribute.attributeID;
                                let _attributeMap = {};
                                let _selectedAttributeList = selectedAttributeList || [];
                                Object.keys(attributeMap).forEach(key => {
                                    _attributeMap[key]=attributeMap[key].map(attribute => {
                                        if(attribute.attributeID === attributeID) {
                                        attribute.selected = false;
                                        }
                                        return attribute;
                                    })
                                });
                                _selectedAttributeList = selectedAttributeList.filter((attribute, index) => {
                                return attribute.attributeID !== attributeID;
                                });
                                this.setState({
                                attributeMap: _attributeMap,
                                selectedAttributeList: _selectedAttributeList
                                });
                            }}></i>
                        </li>
                        }) : null}
                        </MyScroll>
                    </div>
                    <BeiZhu memo={memo} onEntry={(memo) => {
                        this.setState({memo});
                    }}/>
                    </div>
                </TabPane>} 

                {designers && !! designers.length &&
                <TabPane tab="选厨师" key="designer">
                    <div className="chef-block">
                        <div className="choose-chef">
                            <MyScroll width={800} height={434}>
                                {designers.map((designer, index) => {
                                    return <li 
                                        key={index} 
                                        className={classnames({
                                            'card': true,
                                            'iconfont': true,
                                            'select': designer.designerID === designerID
                                        })}
                                        onClick={() => {
                                            this.setState({designerID: designer.designerID});
                                        }}
                                    >
                                        <div className="chef-pic">
                                            {designer.picture ?
                                            <img src={designer.picture} role="presentation" /> :
                                            <i className="iconfont icon-xuanchushi_icon_toux"></i>}
                                        </div>
                                        <h3 className="table-name">{designer.designerName}</h3>
                                        <p className="add-price">+{designer.produceCost}元/份</p>
                                        <div className="card-footer">
                                            <Rate value={designer.designerLevel} disabled />
                                            <div className="favour">
                                                <i className="iconfont icon-xuanchushi_icon_zan-"></i>
                                                <span>{designer.praiseNum}</span>
                                            </div>
                                        </div>
                                    </li>
                                })}
                            </MyScroll>
                        </div>
                    </div>
                </TabPane>}

                <TabPane tab="厨房状态" key="tag">
                    <div className="kitchen-state">
                        {['20','21','23',694].map(tag => {
                            let text;
                            let tagStr = tagIDs || ''
                            switch(tag) {
                                case '20':
                                    text='免做';
                                    break;
                                case '21':
                                    text='先做';
                                    break;
                                case '23':
                                    text='打包';
                                    break;
                                default: 
                                    text='等叫'
                            }
                            return <div 
                                key={tag}
                                className={classnames({
                                    'myBtn-277-44': true,
                                    'btn-active': tagStr.indexOf(tag)>-1 || (tag === 694 && produceStatus === 694)
                                })}
                                onClick={() => {
                                    if(tag === 694) {
                                        let status = dishes.needWeigh ? 830 : 693;
                                        this.setState({produceStatus: produceStatus === 694 ? status : 694});
                                    }else {
                                        if(tagStr.indexOf(tag)>-1) {
                                            tagStr = tagStr.split(',').filter(tagID => {
                                                return tagID !== tag;
                                            }).join(',');
                                        }else {
                                            tagStr = tagIDs ? (tagIDs+','+tag) : tag;
                                        }
                                       
                                        this.setState({tagIDs: tagStr});
                                    }
                                }}
                            >
                                {text}
                            </div>
                        })}
                    </div>
                </TabPane> 

                {dishes.aLaCarteMethod !== 686 &&
                <TabPane tab="赠菜" key='handsel'>
                    <div className="zeng-cai-block">
                        <ChangeNumber 
                            min={1}
                            max={dishes.quantity}
                            onChange={(value) => {
                                this.setState({handselQuantity: value});
                            }}
                        >
                            <div className="zhuang-left">可赠<i className="red">{dishes.quantity}</i>份</div>
                        </ChangeNumber>
                        <div className="select-items">
                            <div className="select-header">赠菜原因</div>
                            <MyScroll width={798} height={148}>
                                {handselReasonList && !!handselReasonList.length &&
                                handselReasonList.map((reason, index) => {
                                    return <li 
                                        key={reason.reasonID}
                                        className={classnames({
                                            'cause-btns': true, 
                                            'btn-active': reason.reason === handselReason
                                        })}
                                        onClick={() => {
                                            this.setState({
                                                handselMemo: '',
                                                handselReason: reason.reason
                                            });
                                        }}
                                    >{reason.reason}</li>
                                })}
                            </MyScroll>
                        </div>
                        <textarea 
                            type="text" 
                            placeholder="可输入自定义赠菜原因" 
                            className="cursor-input"
                            value={handselMemo}
                            onChange={(e) => {
                                this.setState({
                                    handselMemo: e.target.value,
                                    handselReason: e.target.value
                                })
                            }}
                        >
                        </textarea>
                    </div>
                </TabPane>}

                {dishes.aLaCarteMethod === 686 &&
                <TabPane tab="取消赠菜" key='cancelHandsel'>
                    <div className="cancle-zeng-cai-block">
                        <div className="cancle-zeng-cai-title">
                            {!dishes.needWeigh && <div className="cancel-zeng-left">可取消<i>{dishes.quantity}</i>份</div>}
                            <div className="cancel-zeng-right">
                                已选
                                <input type="text" value={this.state.handselQuantity} readOnly />份
                            </div>
                        </div>
                        <CommonKeyboard getResult={(value)=>{
                            this.setState({handselQuantity: value*1});
                        }}/>
                    </div>
                </TabPane>}

                {dishes.aLaCarteMethod !== 998 &&
                <TabPane tab="转赠" key="donated">
                    <div className="zhuan-zeng-block">
                        <div className="zhuang-zeng-block">
                            <div className="zhuang-left">转赠<i className="red">{dishes.quantity}</i>份</div>
                        </div>
                        <div className="zhuan-content">
                            <div className='zhuan-content-search'>
                                <input type="text" placeholder="请输入桌台名称进行查询" onKeyUp={(e) => {
                                    this.getUsingTableList(e.target.value);
                                }} />
                                <i className="iconfont icon-order_btn_search"></i>
                            </div>
                            <VerticalTabs>
                                {usingTableList && !!Object.keys(usingTableList).length&&
                                Object.keys(usingTableList).map((key,index) => {
                                    return <TabPane tab={key} key={index}>
                                        <MyScroll width={638} height={222}>
                                            {!!usingTableList[key].length &&
                                            usingTableList[key].map((table, index) => {
                                            let tableName = table.shareTableName && table.tableName !== 
                                            table.shareTableName ? (table.tableName + '-' + table.shareTableName) : table.tableName;
                                                return <li 
                                                    key={index} 
                                                    className={classnames({
                                                        'zhuan-item': true,
                                                        'iconfont': true,
                                                        'mask': false,
                                                        'select': table.tableID === tableID &&
                                                        table.tableName === tableName
                                                    })}
                                                    onClick={() => {
                                                        this.setState({
                                                            tableID: table.tableID,
                                                            tableName: table.tableName
                                                        });
                                                    }}
                                                >
                                                    <h3 className={classnames({
                                                        'table-name': true,
                                                        'small': tableName.length > 9
                                                    })}>{tableName}</h3>
                                                    <p>{table.customerNumber}/{table.defaultPerson}</p>
                                                </li>
                                            })}
                                        </MyScroll>
                                    </TabPane>
                                })}
                            </VerticalTabs>
                        </div>
                    </div>
                </TabPane>}

                {false &&
                <TabPane tab="取消转赠" key="cancelDonated">
                    <div className="cancle-zhuan-zeng-block">
                        <ChangeNumber>
                            <div className="zhuang-left">取消转赠<i className="red">1</i>份</div>
                        </ChangeNumber>
                    </div>
                </TabPane>}

                {((!dishes.variantID && dishes.price) || //不是套餐明细菜有价格的时候
                (dishes.variantID && !dishes.isAddPrice)) &&  //明细套餐有明细价格的时候
                <TabPane tab="临时改价" key="price">
                    <div className="gai-jia-block">
                        <div className="chengzhong-title">
                            <input type='text' className='result' readOnly value={this.state.price} />
                            <p className="danwei">单位：元</p>
                        </div>
                        <CommonKeyboard getResult={(price)=>{
                            this.setState({price})
                        }}/>
                    </div>
                </TabPane>}
            </Tabs>
        </Modal>
    }
}



export default NotOrderPopup;



